	module.exports = {"data":{"post":{"html":"<p><strong>TL;DR:</strong> I migrated my blog from Jekyll to Hexo. This is everything you need to know to do the same.</p>\n<h2>Initializing Hexo in your existing Jekyll project</h2>\n<p>Here's what I did:</p>\n<p>Back up your existing Jekyll blog to a subdirectory so you can wildly make changes with impunity:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">mkdir</span> jekyll.bak\n</code></pre>\n      </div>\n<p>Now move all the non-git files in the directory into <code>jekyll.bak</code>. Initially I just did it in finder, but then I got curious about how to do it in bash. So if you want to just run a nifty command here you go:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">find</span> <span class=\"token keyword\">.</span> -maxdepth 1 -mindepth 1 <span class=\"token operator\">!</span> -name <span class=\"token string\">'.git'</span> <span class=\"token operator\">!</span> -name <span class=\"token string\">'jekyll.bak'</span> -exec <span class=\"token function\">mv</span> <span class=\"token string\">'{}'</span> ./jekyll.bak/ \\<span class=\"token punctuation\">;</span>\n</code></pre>\n      </div>\n<!-- more -->\n<p>We keep the <code>.git/</code> dir around so that we can cleanly iterate towards a Hexo blog from our Jekyll blog while maintaining all previous version control history. However, the <code>hexo init</code> command actually removed <code>.git/</code> on my system so we need to move it too, but only temporarily:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">mv</span> .git .git.bak\n</code></pre>\n      </div>\n<p>Now initialize Hexo in your project directory:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>hexo init <span class=\"token keyword\">.</span>\n</code></pre>\n      </div>\n<p>This should have left <code>jekyll.bak</code> and <code>.git.bak</code> intact while adding a bunch of new files. Now it's safe to bring back your Git history and add a commit:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">mv</span> .git.bak .git\n<span class=\"token function\">git</span> add <span class=\"token keyword\">.</span>\n<span class=\"token function\">git</span> commit -m <span class=\"token string\">'Add Hexo boilerplate'</span>\n</code></pre>\n      </div>\n<p>Now we've succesffully initialize Hexo. From here on out everything we do will to customize Hexo to suit our needs.</p>\n<p>To make sure everything is running properly run the dev server:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code>hexo server\n</code></pre>\n      </div>\n<h2>Migrating Content</h2>\n<p>Now let's get all your blog posts migrated over to the new system. When running the server you should see the Hello World post as your first post on the homepage. You will also see the default Hexo theme. Here's what that looks like:</p>\n<p><img src=\"https://dropsinn.s3.amazonaws.com/Screen%20Shot%202016-11-12%20at%203.09.34%20PM.png\" alt=\"Default hexo blog\"></p>\n<p>That \"Hello World\" post is the only post in your blog. Hexo keeps all your posts at <code>source/_posts</code>. Let's remove the default post.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">rm</span> source/_posts/hello-world.md\n</code></pre>\n      </div>\n<p>Now, since Hexo and Jekyll are very similar in how they store your posts it's very straightforward to migrate all your content. First, copy it all over. If you also used <code>jekyll.bak</code> to backup your original blog then you can do this:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">mv</span> jekyll.bak/_posts/* source/_posts/\n</code></pre>\n      </div>\n<p>Second, update your Hexo settings to recognize Jekyll-style post filenames. In <code>_config.yml</code> edit the line for <code>new_post_name</code>:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-yaml\"><code><span class=\"token key atrule\">new_post_name</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">:</span>year<span class=\"token punctuation\">-</span><span class=\"token punctuation\">:</span>month<span class=\"token punctuation\">-</span><span class=\"token punctuation\">:</span>day<span class=\"token punctuation\">-</span><span class=\"token punctuation\">:</span>title.md <span class=\"token comment\" spellcheck=\"true\"># File name used to identify posts</span>\n</code></pre>\n      </div>\n<p>Now restart the Hexo server and you should see all your content ðŸ˜Ž</p>\n<h2>Gripes</h2>\n<p>Hexo is not without its shortcomings. I'll outline those here, but take this with a grain of salt because I just started using Hexo yesterday so it may be that my gripes are due to lack of understanding rather than actual problems with the framework.</p>\n<h3>Not simple to create standalone pages</h3>\n<p>Hexo is highly content focused, meaning when you create a new \"page\" you are actually just creating a new Markdown file with some content. In my experience the \"content first\" approach is perfectly suited to blog posts, but not necessarily to pages which are often unique in layout.</p>\n<p>For example, I wanted to create a <code>/tags</code> page. This is simple enough using the Hexo API in a layout file:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-erb\"><code><% site.tags.each((tag) => { %>\n  <a class='tag' href='<%= url_for(tag.path) %>'>\n    <%= tag.name %>\n  </a>\n<% }) %></code></pre>\n      </div>\n<p>But I wanted to be able to do this on one single page. I.e. instead of writing markdown I wanted to write the above code and output all my tags. This didn't work, so I ended up creating a workaround where my <code>tags.md</code> file does nothing more than specify the layout I want to render.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>---\ntitle: Tags\nlayout: tags\n---</code></pre>\n      </div>\n<p>This may seem minor, but I would have liked to be able to simply put the above EJS code into my \"page\" file and had it render.</p>\n<h3>Not simple to create helper functions</h3>\n<p>More than once I found myself wanting to refactor some view logic out of the EJS templates and into an external helper function. As of now it's still unclear to me how to do this. I ended up writing all my code inline in the EJS files but this is not pretty.</p>\n<h3>Documentation</h3>\n<p>This is a tough one since Hexo actually has quite a bit of documentation online. However, I found it didn't readily cover the things I really cared about. Namely, in-depth guides to creating themes.</p>\n<p>For example, things like:</p>\n<ul>\n<li>How to loop through various content types (posts, tags, categories, etc)</li>\n<li>How to sort, filter, etc, those content types</li>\n<li>What methods and properties are available on all the Hexo objects</li>\n</ul>\n<p>I ended up discovering these things through trial and error or viewing other peoples themes. This is fine, but it seems that it could be greatly improved to limit the time it takes someone new to the framework to get comfortable with it.</p>\n<h2>Development Takeaways</h2>\n<p>Regarding layouts and templating: If you want to create custom pages then create a new layout file with the desired name (say <code>about.ejs</code>) and then specify that layout file in the post/page markdown file:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>---\nlayout: about\n---</code></pre>\n      </div>\n<p>Regarding helper functions: I didn't find a good way to add helpers yet but I haven't yet read through all the docs. Who knows, maybe there is an elegant solution.</p>\n<h2>Why</h2>\n<p>I've been meaning to redo my blog for a while. Jekyll does it's job quite well, but I never liked liquid templates and my existing blog has needed some work for quote some time. For example, code snippets when rendered in post excerpts sometimes caused unclosed <code>&#x3C;pre></code> tags, which would really mess up rendering for the whole index page.</p>\n<p>Also, JavaScript. I write it all the time so it makes sense to use it for my blog. I also like the emphasis out of the box on Stylus as opposed to Sass or Less.</p>\n<p>Lastly, a good friend of mine recently told me he uses Hexo for his blog. Personal recommendations go a long way.</p>\n<h2>Outcome</h2>\n<p>So far I've been quite happy. Things I like:</p>\n<ul>\n<li>Drafts</li>\n<li>CLI for creating content</li>\n<li>Simple, multi-language support</li>\n<li>Powered by JavaScript</li>\n</ul>","frontmatter":{"created":"2016-11-16T08:00:00.000Z","title":"Migrating from Jekyll to Hexo"}}},"pathContext":{"id":"/Users/jandrix/dev/sites/blog.iansinnott.com/content/_posts/2016-11-16-migrating-from-jekyll-to-hexo.md absPath of file >>> MarkdownRemark","next":{"frontmatter":{"title":"Today i forgot my coffee","created":"2016-11-16T08:00:00.000Z"},"slug":"today-i-forgot-my-coffee"},"prev":{"frontmatter":{"title":"A refreshing dose of minimalism","created":"2016-11-16T08:00:00.000Z"},"slug":"a-refreshing-dose-of-minimalism"}}}

